

import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

import axios from 'axios'
import DD_CONFIGS from '@/utils/dd-config'
import baseLoading from 'components/base-loading.vue';
import {getServiceHomePic, appUpdateSce} from '@/api/home';
import draggable from 'vuedraggable'
import WX_CONFIGS from '@/utils/wx-config';

export default {
components: {
Swiper, SwiperItem, GroupTitle, Grid, GridItem, baseLoading, draggable, Badge
},
name: 'home',
data() {
return {
isLoadingData: true,
slideShowList: [],
quickMenuList: [],
swiperItemIndex: 1,
slideShow_index: 0,
commonsApplicationList: [],
newTip: false,
}
},
methods: {
slideShow_onIndexChange(index) {
this.slideShow_index = index
},
goTarget(item) {
window.localStorage.setItem('workflowStatus', "1");
window.localStorage.setItem('applyStatus', "1");
if (item.menuName === "问与答") {
window.location.href = item.menuResource;
} else {
let selectItem = item.menuResource.split("/#/")[1];
if (selectItem) {
this.$router.push('/' + selectItem);
} else {
this.$router.push('/' + item.menuResource);
}
}
},
getHome() {

let that = this;
getServiceHomePic().then(response => {
if (response.data && response.data.rows) {
response.data.rows.forEach(function (data) {
that.slideShowList.push({img: data.picUrl})
});
that.isLoadingData = false;
} else {
that.isLoadingData = false;
}
}).catch(error => {
that.isLoadingData = false;
});

appUpdateSce({"appEquipment": "Android"}).then(response => {
if (response.data && response.data) {
this.quickMenuList = response.data.menuCateories;
this.commonsApplicationList = response.data.menuCateories.commonsApplication[0].menus;
this.commonsApplicationList.push({
"menuIcon": require("../assets/images/home/addApp@3.png"),
"menuResource": "homeManage",
"menuName": "添加",
});
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
})
}
},
mounted() {
console.log(JSON.stringify(process.env));
/**
*  获取token userId
*/
WX_CONFIGS.getToken().then((response) => {
console.log("response:", JSON.stringify(response));
if (response.access_token && response.userId) {
window.localStorage.token = response.access_token;
window.localStorage.userId = response.userId;
this.getHome();
}
}).catch(error => {
this.isLoadingData = false;
this.$vux.toast.text("认证失败，请重新登录。", 'middle');
});

try {
let that = this;
dd.ready(function () {

DD_CONFIGS.ddConfig(window.location.href.split('#')[0]);
/*
*在dd.config()验证通过的情况下，就会执行ready()函数，
*dd.ready参数为回调函数，在环境准备就绪时触发，jsapi的调用需要保证在
*该回调函数触发后调用，否则无效,所以所你会发现有对jsapi接口的调用都会在
*ready的回调函数里面
*/

dd.runtime.permission.requestAuthCode({
corpId: window.localStorage.getItem('corpId'),
onSuccess: function (result) {
console.log("这里是code：" + JSON.stringify(result.code));
let loginUrl;
if (window.location.hostname === 'localhost') {
loginUrl = process.env.baseUrl + 'oauth/token?client_id=client&client_secret=secret&grant_type=password&username=admin&password=admin';
} else {
loginUrl = process.env.baseUrl + 'oauth/token?client_id=client&client_secret=secret&grant_type=password&username=' + result.code + '&password=' + result.code;
}
let instance = axios.create({
timeout: 30000,
headers: {
'Content-Type': 'application/x-www-form-urlencoded'
}
});
instance.post(loginUrl).then(function (response) {

if (response && response.data && typeof response.data === 'object' && response.data.access_token) {
window.localStorage.setItem('DD_ACCESS_TOKEN', response.data.access_token);
/*console.log("这里是getitem"+ window.localStorage.setItem('DD_ACCESS_TOKEN', response.data.access_token));*/
/* alert("这里进入首页钉钉的---access_token"+ response.data.access_token);*/
that.isLoadingData = false;
}
}, function (error) {

}).catch(function (error) {

});
},
onFail: function (err) {
console.log(JSON.stringify(err));
}
});
});

dd.error(function (error) {
console.log('dd error: ' + JSON.stringify(err));
});
} catch (e) {
console.log(e);
}
}

}




